'use client';

import { useEffect, useMemo, useState } from 'react';
import { useReactTable, getCoreRowModel, getFilteredRowModel, getPaginationRowModel, getSortedRowModel } from '@tanstack/react-table';
import { Pencil, Trash2, Search, X, Eye } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardToolbar, CardTable, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { ScrollArea, ScrollBar } from '@/components/ui/scroll-area';
import { DataGrid, DataGridTable, DataGridPagination } from '@/components/ui/data-grid';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';

export default function PostTable() {
    const [posts, setPosts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [pagination, setPagination] = useState({ pageIndex: 0, pageSize: 10 });
    const [sorting, setSorting] = useState([]);
    const [searchQuery, setSearchQuery] = useState('');

    // Fetch posts
    useEffect(() => {
        const fetchPosts = async () => {
            try {
                setLoading(true);
                const token = localStorage.getItem('token');
                if (!token) throw new Error('Token not found');

                const res = await fetch(`https://wiqiapi.testenvapp.com/api/admin/getPost?limit=${pagination.pageSize}&page=${pagination.pageIndex + 1}`, {
                    headers: { Authorization: `Bearer ${token}` },
                });

                const data = await res.json();
                if (!res.ok || !data.success) throw new Error(data.message || 'Failed to fetch');

                setPosts(data.data.posts || []);
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchPosts();
    }, [pagination.pageIndex, pagination.pageSize]);

    // Filtered data
    const filteredData = useMemo(() => {
        if (!searchQuery) return posts;
        return posts.filter(
            p =>
                p.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
                p.type.toLowerCase().includes(searchQuery.toLowerCase()) ||
                p.user.firstName.toLowerCase().includes(searchQuery.toLowerCase()) ||
                p.user.lastName.toLowerCase().includes(searchQuery.toLowerCase())
        );
    }, [searchQuery, posts]);

    // Columns
    const columns = useMemo(() => [
        {
            id: 'image',
            accessorFn: row => row.image,
            header: () => <span className="font-bold">Image</span>,
            cell: ({ row }) => row.original.image ? (
                <img src={row.original.image} alt={row.original.title} className="w-12 h-12 rounded object-cover border" />
            ) : <span className="text-gray-400 text-sm">No Image</span>,
            size: 100,
        },
        {
            id: 'title',
            accessorFn: row => row.title,
            header: () => <span className="font-bold">Title</span>,
            cell: ({ row }) => row.original.title,
            size: 200,
        },
        {
            id: 'type',
            accessorFn: row => row.type,
            header: () => <span className="font-bold">Type</span>,
            cell: ({ row }) => <span className="capitalize">{row.original.type}</span>,
            size: 100,
        },
        {
            id: 'view',
            accessorFn: row => row.view,
            header: () => <span className="font-bold">Views</span>,
            cell: ({ row }) => row.original.view,
            size: 80,
        },
        {
            id: 'user',
            accessorFn: row => row.user.firstName,
            header: () => <span className="font-bold">User</span>,
            cell: ({ row }) => `${row.original.user.firstName} ${row.original.user.lastName}`,
            size: 150,
        },
        {
            id: 'createdAt',
            accessorFn: row => row.createdAt,
            header: () => <span className="font-bold">Created At</span>,
            cell: ({ row }) => new Date(row.original.createdAt).toLocaleDateString(),
            size: 120,
        },
        {
            id: 'actions',
            header: () => <span className="font-bold text-right">Actions</span>,
            cell: ({ row }) => (
                <div className="flex justify-end gap-2">
                    <TooltipProvider>
                        <Tooltip>
                            <TooltipTrigger asChild>
                                <button className="text-primary hover:text-blue-800"><Eye size={16} /></button>
                            </TooltipTrigger>
                            <TooltipContent>View Post</TooltipContent>
                        </Tooltip>
                    </TooltipProvider>
                    <TooltipProvider>
                        <Tooltip>
                            <TooltipTrigger asChild>
                                <button className="text-red-600 hover:text-red-800"><Trash2 size={16} /></button>
                            </TooltipTrigger>
                            <TooltipContent>Delete Post</TooltipContent>
                        </Tooltip>
                    </TooltipProvider>
                </div>
            ),
            size: 100,
        }
    ], []);

    const table = useReactTable({
        data: filteredData,
        columns,
        state: { pagination, sorting },
        getRowId: row => row.id,
        pageCount: Math.ceil(filteredData.length / pagination.pageSize),
        onPaginationChange: setPagination,
        onSortingChange: setSorting,
        getCoreRowModel: getCoreRowModel(),
        getFilteredRowModel: getFilteredRowModel(),
        getPaginationRowModel: getPaginationRowModel(),
        getSortedRowModel: getSortedRowModel(),
        columnResizeMode: 'onChange',
    });

    if (loading) return <div className="flex justify-center items-center min-h-[200px]">Loading posts...</div>;
    if (error) return <div className="p-4 bg-red-100 text-red-700 rounded">{error}</div>;

    return (
        <DataGrid table={table} recordCount={filteredData.length}>
            <Card>
                <CardHeader className="py-3.5">
                    <CardTitle>Posts</CardTitle>
                    <CardToolbar className="relative">
                        <Search className="absolute start-2 top-1/2 -translate-y-1/2 text-primary" />
                        <Input
                            placeholder="Search posts..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="ps-9 max-w-[300px] w-full"
                        />
                        {searchQuery && (
                            <Button variant="ghost" className="absolute end-1.5 top-1/2 -translate-y-1/2 h-6 w-6" onClick={() => setSearchQuery('')}>
                                <X />
                            </Button>
                        )}
                    </CardToolbar>
                </CardHeader>
                <CardTable>
                    <ScrollArea>
                        <DataGridTable />
                        <ScrollBar orientation="horizontal" />
                    </ScrollArea>
                </CardTable>
                <CardFooter>
                    <DataGridPagination />
                </CardFooter>
            </Card>
        </DataGrid>
    );
}
